.menuContainer {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 8fr 1fr;
  background: url(/weddingapp/static/media/menubg.c6e0b8fa.jpg) repeat-x scroll 0 0;
  justify-content: end;
}

.menu {
