}

.mobileNavigationText {
  background-color: #f9bb22;
  border-bottom: 1px solid #cf9300;
  padding: 7px 2%;
  font-size: 15px;
  font-weight: normal;
}

.mobileNavigationText a {
  display: block;
}

.mobileNavigationText:hover {
  background-color: #cf9300;
  cursor: pointer;
}

.mobileNavigationText a {
  color: #333333;
  text-decoration: none;
}

.Navigation {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1.5fr;
  background: url(/weddingapp/static/media/menubg.c6e0b8fa.jpg) repeat-x;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
}

.home {
  margin-left: 15px;
}

.navigationItem {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 2fr 1fr;
  grid-column-gap: 2px;
  -webkit-column-gap: 2px;
          column-gap: 2px;
  text-align: center;
}

.largeNavigationItem {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 7fr 1fr;
  text-align: center;
}

.navigationText {
  padding: 15px 0px;
  text-align: center;
  text-decoration: none;
  color: #333333;
}

.navigationText:hover {
  background: url(/weddingapp/static/media/menu_hover.2c665cfb.jpg) repeat-x;
  cursor: pointer;
  color: white;
}

.active {
  background: url(/weddingapp/static/media/menu_hover.2c665cfb.jpg) repeat-x;
  color: white !important;
}

.lastNavigationItem {
  padding: 15px 0px;
  text-align: center;
}

.lastNavigationItem a {
  text-decoration: none;
  color: #333333;
}

.lastNavigationItem:hover {
  background: url(/weddingapp/static/media/menu_hover.2c665cfb.jpg) repeat-x;
  cursor: pointer;
  color: white;
}

.menuSeperator {
  display: grid;
  align-items: center;
}

@media screen and (min-width: 757px) and (max-width: 1257px) {
  .navigationText {
